import {useEffect, useState} from 'react';
import {useHistory} from 'react-router-dom';
import {isUserLoggedIn} from "../service/LoginService";
import NotesService from '../service/NotesService';
import CategoryService from '../service/CategoryService';
import Space from "../element/Space";
import SortNotesService from "../service/SortNotesService";
import {PropagateLoader} from "react-spinners";
import {saveCategory} from "../service/CategoryService";

const NotesList = () => {
    const [notes, setNotes] = useState([]);
    const [loading, setLoading] = useState(true);
    const [category, setCategory] = useState('');
    const history = useHistory();
    const isAuth = isUserLoggedIn();

    useEffect(() => {
        if (isAuth) {
            NotesService.getAll()
                .then(response => {
                    //console.log('Printing response!', response.data);
                    setNotes(response.data);
                    setLoading(false);
                    getSaveCategory();
                    saveCategory(category);
                })
                .catch(error => {
                    console.log('An error occurred!', error);
                })
        } else {
            alert("Log in first!");
            history.push("/radoslaw-sawicki-frontend-react-notesapp");
        }
    }, [loading]);

    function getSaveCategory() {
        CategoryService.getAll()
            .then(response => {
                //console.log('Printing response!', response.data);
                const foundCategory = response.data.findLast(obj => {
                    return obj.categoryName;
                });
                if (foundCategory) {
                    let category = foundCategory.categoryName;
                    setCategory(category);
                    console.log('Saved category is: ' + category + '!');
                }
            })
            .catch(error => {
                console.log('An error occurred!', error);
            })
    }

    function saveSelectedCategory() {
        saveCategory(category);
        console.log('Selected category is: ' + category + '!');
    }

    return (
        <div className="main-content">
            {loading ? (
                <div className="loader-container" style={{marginTop: 77}}>
                    <div className="text-center">
                        <PropagateLoader color={'#79589f'} size={20}/>
                        <Space/>
                    </div>
                </div>
            ) : (
                <div className="main-content">
                    <h4 className="text-center">List of Notes</h4>
                    <Space/>
                    <div className="text-center">
                        <select
                            id="category"
                            className="main-category"
                            value={category}
                            onClick={saveSelectedCategory}
                            onChange={(e) => setCategory(e.target.value)}>
                            <option value="all">All categories</option>
                            <option value="blogging">Blogging</option>
                            <option value="congregation">Congregation</option>
                            <option value="circuit">Circuit</option>
                            <option value="meeting">Meeting</option>
                            <option value="programming">Programming</option>
                            <option value="other">Other</option>
                            <option value="vacation">Vacation</option>
                        </select>
                    </div>
                    <div className="notes-list mt-4">
                        {
                            <SortNotesService
                                notes={notes}
                                category={category}
                            />
                        }
                    </div>
                    <Space/>
                </div>
            )}
        </div>
    );
}

export default NotesList;